@import url(https://fonts.googleapis.com/css?family=Poppins:300);
@import url(https://use.fontawesome.com/releases/v5.7.2/css/brands.css);
@import url(https://use.fontawesome.com/releases/v5.7.2/css/fontawesome.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#quote-box {
  text-align: center;
  padding: 50px;

}

.bounce {
  opacity: 0
}

.bounceIn {
  opacity: 1
}
.bounceOut {
  opacity: 0
}

#text {
  color: #FAF7F5;
  font-size: 3em;
  text-shadow: 0.5px 0.2px #9bbfc6;
  transition: opacity 500ms 0ms;
}

#author {
  color: #FAF7F5;
  transition: opacity 500ms 0ms;
}

#new-quote {
  background-color: #AAD1D9;
  color: #FAF7F5;
  width: 30%;
  height: 2rem;
  margin: 10px;
  font-size: 1.25rem;
}

.buttons {
  margin: auto;
  margin-bottom: 30px;
  position: absolute;
  left: 0; bottom: 0; right: 0;
}

#tweet-quote {
  background-color: inherit;
  border: none;
}

i {
  color: white;
}

body {
  background-color: #FD665C;
  font-family: 'Poppins', sans-serif;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes bounceIn {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0;
  }
  60% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes bounceIn {
  0% {
    -webkit-transform: scale(0.1);
            transform: scale(0.1);
    opacity: 0;
  }
  60% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    opacity: 1;
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

